'use client'

import { useEffect } from 'react'



export default function Home() {
  useEffect(() => {
    const logo = document.getElementById("logo") as HTMLImageElement
    const themeIcon = document.getElementById("theme-icon")

    const applyTheme = (isDark: boolean) => {
      document.body.classList.toggle("dark", isDark)
      logo.src = isDark ? "/assets/logo-dark.svg" : "/assets/logo-light.svg"
      themeIcon?.setAttribute("data-feather", isDark ? "sun" : "moon")
      localStorage.setItem("theme", isDark ? "dark" : "light")
      window.feather && window.feather.replace()
    }

    const toggleTheme = () => {
      const isDark = document.body.classList.contains("dark")
      applyTheme(!isDark)
    }

    const savedTheme = localStorage.getItem("theme") || "light"
    applyTheme(savedTheme === "dark")

    const btn = document.getElementById("darkToggleBtn")
    btn?.addEventListener("click", toggleTheme)
  }, [])

  return (
    <>
      <header>
        <a href="#" className="logo">
          <img src="/assets/logo-light.svg" id="logo" alt="AnalyDocs Logo" />
        </a>
        <nav>
          <a href="#features">Features</a>
          <a href="#">Pricing</a>
          <a href="#">Sign in</a>
          <a href="editor.html" className="cta">Get started</a>
          <button className="dark-toggle" id="darkToggleBtn" title="Toggle Theme">
            <i data-feather="moon" id="theme-icon"></i>
          </button>
        </nav>
      </header>

      <section className="hero">
        <section className="hero-content">
          <h1>Create intelligent documents with AI</h1>
          <p>Generate, visualize, and edit documents effortlessly using simple, powerful AI and an easy-to-use designer.</p>
          <div className="buttons">
            <a href="editor.html" className="primary">Get started</a>
            <a href="#" className="secondary">Watch demo</a>
          </div>
        </section>
      </section>

      <section className="features" id="features">
        <h2>Features</h2>
        <div className="feature-grid">
          <div className="feature">
            <h3>ðŸ§  AI-powered generation</h3>
            <p>Create entire documents from natural language prompts in seconds.</p>
          </div>
          <div className="feature">
            <h3>ðŸ“Š Visual editor</h3>
            <p>Construct documents visually â€” with charts, tables, images, and more.</p>
          </div>
          <div className="feature">
            <h3>ðŸ§© Drag and drop</h3>
            <p>Rearrange content effortlessly using drag-and-drop actions.</p>
          </div>
        </div>
      </section>

      <section className="call-to-action">
        <h3>Start building your first document today</h3>
        <p>Sign up now and create your first document in minutes.</p>
        <a href="editor.html">Get started</a>
      </section>

      <footer>
        &copy; 2025 AnalyDocs Inc. All rights reserved.
      </footer>
    </>
  )
}
